export default {
  name: 'app',
  data () {
    return {
      transitionName: "fadeInRight"
    }
  },
  watch: {
    '$route' (to, from) {
      const toDepth = to.path.length;
      const fromDepth = from.path.length;
      this.transitionName = toDepth > fromDepth ? 'fadeOutLeft' : 'fadeOutRight';
    }
  }
}



